import axios from "axios";
// axios.defaults.headers["Authorization"] = "token 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";
// axios
const service = axios.create({
  // 
  timeout: 10000,
});

// request
service.interceptors.request.use(
  (config) => {
    return config;
  },
  (error) => {
    Promise.reject(error);
  }
);

// 
service.interceptors.response.use(
  (res) => {
    return res
  },
  (error) => {
    return error
  }
);

export default service;
